.main__container {
    width: 100%;
    height: 100%;
    padding-bottom: 50px;
}
.header__title {
    text-align: center;
    white-space: nowrap;
    font-size: 40px;
    font-weight: 700;
    margin-top: 50px;
}
.login__container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 400px;
    height: 400px;
    background-color: #e9e9ff;
    border-radius: 40px;
    margin-bottom: 20px;
    padding: 40px 10px;
}
.login {
    display: flex;
    justify-content: center;
    margin-top: 50px;
}
.form__submit__button {
    width: 300px;
    height: 50px;
    border: none;
    border-radius: 10px;
    background-color: #fe805f;
    font-size: 20px;
    color: #fff;
    cursor: pointer;
    transition: 0.2s linear;
}

.form__submit__button:hover {
    opacity: 0.8;
    box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.3);
}

.form__submit__button:focus {
    outline: none;
}
.label {
    margin: 10px 40px;
    font-size: 20px;
    font-weight: 700;
}
.btn {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.error {
    border: 1px solid red;
}

@media (max-width: 550px) {
    .main__container {
        width: 100%;
        flex-wrap: wrap;
    }
    .header__title {
        display: flex;
        justify-content: center;
    }
}
