@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap" rel="stylesheet');

html {
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6,
li {
  font-family: "Poppins", sans-serif;
  margin: 0;
}
p,
span,
input,
a,
div,
textarea,
button {
  font-family: "Poppins", sans-serif;
  margin: 0;
}

body {
  margin: 0;
  background: #e5e5e5;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  /* color: #334669; */
}

input:focus,
button:focus {
  outline: none;
}

button {
  font-family: "Poppins", sans-serif;
  cursor: pointer;
}

#root {
  display: flex;
  justify-content: center;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

@media only screen and (max-width: 1000px) {
  html {
    font-size: 15px;
  }
}
@media only screen and (max-width: 700px) {
  html {
    font-size: 14px;
  }
}
@media only screen and (max-width: 400px) {
  html {
    font-size: 13px;
  }
}
